
<template>
    <div class="fragment">
          <div class="max_title">{{language.syntheticFragment}}</div>
          <div class="fragmentBox">
                 <div class="left">
                     <img src="../../images/synthewis.png" alt="">
                 </div>

                 <div class="right">
                      <div class="title"> NFT </div>
                      <div class="title_txt">{{language.fragmentsCanBeConverted}} </div>  
                      <div class="ratio"><span>{{language.subscriptionRatio}}</span>  12{{language.pieces_20}} = 1{{language.individual}}NFT </div>
                   
                    <div class="synthesis">
                          <div class="plus_reduce">
                            <!-- <div class="plus"> 
                                  <div class="input_box">
                                      <img src="../../images/jian.png" alt="" @click="reduceFn">
                                      <input type="text"  v-model="exchangeTxt"> 
                                      <img src="../../images/j.png" alt="" @click="plusFn" >
                                  </div>
                                  <div class="selected">当前余额不足，无法选择</div>
                            </div>   -->
<!-- 
                           <div class="reduce"> 
                              <div class="fragment-top" @click="syntheticFragment">  合成碎片 </div> 
                                 <div class="get-Fragment">  
                                    获取碎片
                                    <img src="../../images/farm/share.png" alt="">  
                                 </div> 
                           </div> -->
                            

                        <div class="btn reduce ">
                              
                             <div class="get-Fragment">
                                 <m-button :loading="giveAdd" @click="syntheticFragment" class="hdr-el-blue" :height="36"
                                    style="width: 100px;height:43px"
                                 >
                                    {{language.synthesis_status}}
                                 </m-button>
                             </div>
                               <div class="mysty">
                                  <span>{{language.getFragment}}</span> 
                                  <img src="../../images/farm/share.png" alt=""> 
                              </div> 
                        </div>

                         </div>
                      </div>

                        <div class="currently-have"> 
                              <div class="fragment-top">{{language.currentlyHave}}{{queryQuantity}}{{language.pieces_fragment}}，{{resNumber}}--NFT</div> 
                        </div> 
                 </div>
          </div>  


      <propSynthesis :synthesisStatus="synthesisStatus" @synthesisFn="synthesisFn">
           
      </propSynthesis>

    </div>
</template>

<script>
import MButton from "@/components/mButton";
import propSynthesis from "./comps/propSynthesis.vue"
import { mapState, mapGetters } from "vuex";
export default {
    data(){
       return {
         exchangeTxt:1,
         synthesisStatus:false,
         queryQuantity:"",
         giveAdd:false,
         resNumber:0,
       }
    },
    components:{
         propSynthesis,
         MButton
    },
    created(){
       this.initNumber();
       this.initNft();
    },
     computed: {
    ...mapState("app", ["address"]),
    ...mapGetters(["language"]),
  },

    methods:{
       //   查询数量
       initNumber(){
            this.$api.queryFragmentQuantity().then((res)=>{
               this.queryQuantity = res/1e18 ;
            })
        },
        initNft(){
          this.$api.getNFTBalance().then((res)=>{
             this.resNumber = res 
          })
        },
        //  合成
        syntheticFragment(){
           if( this.queryQuantity < 12 ){
               return this.$toast.error("当前nft碎片不足")
           }
            this.giveAdd  = true;
            this.$api.fragmentQuantitySynthesis().then(()=>{
                 this.giveAdd  = false;
                 this.initNumber();
                 this.initNft();
                 this.synthesisStatus = true;
            }).catch(()=>{
                 this.giveAdd  = false;
          });
        },
      //   syntheticFragment(){
      //       this.synthesisStatus = true;
      //   },
        //   减
        reduceFn(){
           if(this.exchangeTxt <= 1){
              return 
            }
           this.exchangeTxt--
        },
        //   加
        plusFn(){
            this.exchangeTxt++
        },
        synthesisFn(){
            this.synthesisStatus = false;
        }
    }

}
</script>
